import React from 'react';
import { Row, Col, Carousel } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle, faClock, faSquare } from '@fortawesome/free-solid-svg-icons';


class CarouselRecipe extends React.Component {
    render() {
        return (
            <Carousel>

                <Carousel.Item>

                    <div className=" card-img img1"> </div>
                    <a href="/recipe">
                        <div className=" description">
                            <Row>
                                <Col xs={12}>
                                    <p className="text-left description-title">Apfelkuchen</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={4}>
                                    <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" /> easy</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info text-center"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> cake</p>
                                </Col>
                                <Col xs={4}>
                                    <p className="sub-info text-right"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> 60min</p>
                                </Col>
                            </Row>
                        </div>
                    </a>

                </Carousel.Item>

                <Carousel.Item>
                    <div className=" card-img img2"> </div>
                    <div className=" description">
                        <Row>
                            <Col xs={12}>
                                <p className="text-left description-title">Waffel</p>
                            </Col>
                        </Row>
                        <Row className="d-felx">
                            <Col xs={4}>
                                <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" /> easy</p>
                            </Col>
                            <Col xs={4}>
                                <p className="sub-info text-center"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> cake</p>
                            </Col>
                            <Col xs={4}>
                                <p className="sub-info text-right"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> 15min</p>
                            </Col>
                        </Row>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div className=" card-img img3"> </div>
                    <div className=" description">
                        <Row>
                            <Col xs={12}>
                                <p className="text-left description-title">Fr√ºhlingsrollen</p>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={4}>
                                <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" /> middle</p>
                            </Col>
                            <Col xs={4}>
                                <p className="sub-info text-center"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> snack</p>
                            </Col>
                            <Col xs={4}>
                                <p className="sub-info text-right"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> 2h</p>
                            </Col>
                        </Row>
                    </div>
                </Carousel.Item>
            </Carousel >
        );
    }
}

export default CarouselRecipe;