import React from 'react';
import { Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle, faClock, faSquare } from '@fortawesome/free-solid-svg-icons';

let data = [
    {
        ReTitle: "Apfelkuchen",
        Level: "easy",
        Type: "cake",
        Time: "60min"
    },
    {
        ReTitle: "Ca Ri Ga",
        Level: "easy",
        Type: "soup",
        Time: "1h"
    },
    {
        ReTitle: "Can Chua Ca",
        Level: "easy",
        Type: "soup",
        Time: "30min"
    },
    {
        ReTitle: "Fr√ºhlingsrollen",
        Level: "middle",
        Type: "snack",
        Time: "2h"
    },
    {
        ReTitle: "Rindfleisch mit Rucola",
        Level: "easy",
        Type: "meat",
        Time: "15min"
    },
    {
        ReTitle: "Thit Kho",
        Level: "middle",
        Type: "meat",
        Time: "30min"
    },
    {
        ReTitle: "Waffel",
        Level: "easy",
        Type: "cake",
        Time: "15min"
    },



];


const Card = (props) => {
    return (

        <Row className="mb-4">
            <Col xs={12}>
                <Col xs={4} className="float-left">
                    <div className="recipeImg">

                    </div>
                </Col>
                <Col xs={12} className="pt-2 info-card ">
                    <div className="info">
                        <Row>
                            <Col xs={12}>
                                <p className="text-left description-title">{props.ReTitle}</p>
                            </Col>
                        </Row>
                        <Row className="d-flex">
                            <Col xs={3}>
                                <p className="sub-info"> <FontAwesomeIcon icon={faCircle} className="icon-easy" />{props.Level}</p>
                            </Col>
                            <Col xs={3}>
                                <p className="sub-info"> <FontAwesomeIcon icon={faSquare} className="icon-cake" /> {props.Type}</p>
                            </Col>
                            <Col xs={3}>
                                <p className="sub-info"> <FontAwesomeIcon icon={faClock} className="icon-clock" /> {props.Time}</p>
                            </Col>
                        </Row>
                    </div>

                </Col>
            </Col>
        </Row>
    )
}


const CardList = (props) => {
    return (
        <div>
            {props.cards.map(card => <Card {...card} />)}
        </div>
    )
}

class RecipeCard extends React.Component {
    render() {
        return (
            <CardList cards={data} />
        )
    }
};


export default RecipeCard;